.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialog {
    padding: 10px;
}
.dialogs .active{
    color: red;
}
.message {
    padding: 10px;
}
.dialogs img {
    width: 50px;
}

.nicknameWrapper {
    display: flex;
    align-items: center;
}

.nickname {
    color: #FFF;
}
.avatar {
    padding-right: 5px;
}

.textarea {
    border-radius: 5px;
    background-color: #a7e1fc;
}

.button {
    background-color: rgb(89, 158, 236);
    border-radius: 5px;
    width: 23%;
    margin: 10px 0;
}

.messagesBlock {
    padding-left: 10px;
}